<template>
  <div class="wyl_big">
    <!-- header   -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!-- header end -->

    <!--   start -->
    <el-card>
      <!-- serach -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="">
            <el-button slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  table  -->
      <el-table :data="ordersList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column lebel="" prop="order_number" width="300"></el-table-column>
        <el-table-column lebel="" prop="order_price"></el-table-column>
        <el-table-column lebel="" prop="pay_status">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status=='1'" type="success"></el-tag>
            <el-tag v-else-if="scope.row.pay_status==0" type="danger"></el-tag>
          </template>
        </el-table-column>
        <el-table-column lebel="" prop="is_send"></el-table-column>
        <el-table-column lebel="" prop="create_time"></el-table-column>
        <el-table-column lebel="">
          <el-button @click="showBox" size="mini" type="primary" icon="el-icon-edit"></el-button>
          <el-button @click="showProgressBox" size="mini" type="success" icon="el-icon-location"></el-button>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="Total"
      ></el-pagination>
    </el-card>

    <!--    end -->
    <!--    start -->
    <el-dialog
      title=""
      :visible.sync="addressDialogVisible"
      @close="addressDialogClosed"
      width="50%"
    >
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
      >
        <el-form-item label="/:" prop="address1">
          <!-- option  props  -->
          <el-cascader v-model="addressForm.address1" :options="cityData"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--     end -->

    <!--     start -->
    <el-dialog
      title=" "
      :visible.sync="progressDialogVisible"
      @close="progressDialogClosed"
      width="50%"
    >
      <el-button></el-button>
    </el-dialog>
    <!--     end -->
  </div>
</template>

<script>
import cityData from "./province_city_county(1).js";
export default {
  name: "",
  data() {
    return {
      // 
      ordersList: [],
      // 
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 10
      },
      // 
      Total: 0,
      //   
      addressDialogVisible: false,
      //   
      addressForm: {},
      //    
      addressFormRules: {
        address1: [
          { required: true, message: "/", trigger: "blur" }
        ],
        address2: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      //  
      address1: [],
      address2: "",
      cityData,
      //     
      progressDialogVisible: false,
      //     
      progressInfo: []
    };
  },
  props: {},
  components: {},
  mounted() {},
  created() {
    this.getOrdersList();
  },
  methods: {
    // 
    async getOrdersList() {
      let { data: res } = await this.$http.get(`orders`, {
        params: this.queryInfo
      });
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error("");
      }
      this.ordersList = res.data.goods;
      this.Total = res.data.total;
    },
    //  pageSize 
    handleSizeChange(a) {
      //   console.log(a);
      this.queryInfo.pagesize = a;
      //   
      this.getOrdersList();
    },
    //   
    handleCurrentChange(a) {
      //   console.log(a);
      this.queryInfo.pagenum = a;
      //   
      this.getOrdersList();
    },
    // 
    showBox() {
      this.addressDialogVisible = true;
    },
    //   
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields();
    },
    // 
    async showProgressBox() {
      this.progressDialogVisible = true;
      let { data: res } = await this.$http.get(`/kuaidi/:id`);
      console.log(res);
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg);
      }
      this.progressInfo = res.data;
    },
    //    
    progressDialogClosed() {
      //   this.$refs.progressFormRef.resetFields();
    }
  }
};
</script>

<stylescoped lang='scss'>
</style>